.modal_content{
  color: white;

  display: flex;
  justify-content: center;

  background-color: transparent;
  border: 2px solid #ffffff70;
  border-radius: 20px;
  backdrop-filter: blur(20px);
  box-shadow: 0 0 1.2rem white;

  max-width: 23rem;
  min-height: 26rem;
  z-index: 100;
  overflow: hidden;
}

.icon_close{
  padding: 0;

  position: absolute;
  top: 0;
  right: 0;
  width: 2.4rem;
  height: 2.4rem;

  font-size: 1.9rem;
  background-color: white;
  color: black;
  border-radius: 0 19px 0 19px;
  border: 1.5px solid transparent;

  transition: 0.4s;
}
.icon_close:hover{
  background-color: black;
  color: white;
  border: 1.5px solid white;
}


.login{
  width: 100%;
  padding: 1.5rem;
}

.login h2{
  font-size: 2rem;
  text-align: center;
}



.input_box{
  width: 100%;
  height: 2.5rem;
  border-bottom: 2px solid white;
  margin: 2rem 0;
}

.input_box label{
  position: absolute;
  left: 1.7rem;
  transition: 0.4s;
  transform: translateY(20%);
}

.input_box input:focus~label, .input_box input:valid~label{
  transform: translateY(-75%);
}

.input_box input{
  padding-right: 1.4rem;
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  color: white;
  font-weight: 600;
}

.input_box .icon{
  position: absolute;
  right: 1.5rem;
  font-size: 1.2rem;
  line-height: 2.5rem;
}


.remember_forgot{
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
}
.remember_forgot label input{
  accent-color: white;
  margin-right: 3px;
}
.forgot_link{
  color: white;
  text-decoration: none;
}
.forgot_link:hover{
  text-decoration: underline;
}


.submit_btn{
  margin: 0.8rem 0;
  width: 100%;
  height: 3rem;
  border: 1.5px solid transparent;
  border-radius: 6px;
  font-size: 1.1rem;
  font-weight: 500;
  transition: 0.4s;
}
.submit_btn:hover{
  background-color: black;
  color: white;
  border: 1.5px solid white;
}


.login_register{
  text-align: center;
  font-size: 0.9rem;
}
.register_link{
  color: white;
  background: transparent;
  border: none;
  font-weight: 500;
}
.register_link:hover{
  text-decoration: underline;
}